import { useState } from "react";

import "./App.css";

import PageHome from "./PageHome";
import PageMy from "./PageMy";
import useMy from "./hooks/useMy";

import stocks from "./stock.json";

function App() {
  const [route, setRoute] = useState("/");
  const { my, setMy } = useMy();

  const handleRouteChange = (pageName) => {
    if (pageName !== route) {
      setRoute(pageName);
      window.location.hash = "";
    }
  };

  if (route === "my") {
    return (
      <PageMy
        stocks={stocks}
        handleRouteChange={handleRouteChange}
        my={my}
        setMy={setMy}
      />
    );
  }

  return (
    <PageHome
      stocks={stocks}
      handleRouteChange={handleRouteChange}
      my={my}
      setMy={setMy}
    />
  );
}

export default App;
