import classnames from "classnames";
import { Fragment, useEffect, useState } from "react";
import { Button, Divider, Space } from "antd";

import ModuleDetail from "../ModuleDetail";

import styles from "./index.module.css";

const l1Sort = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

const listToTree = (list, level) => {
  const tree = {};
  list.forEach((item) => {
    const { l1_name, l2_name, l3_name } = item;
    if (!tree[l1_name]) {
      tree[l1_name] = {};
    }
    if (level >= 2) {
      if (!tree[l1_name][l2_name]) {
        tree[l1_name][l2_name] = {};
      }
    }
    if (level >= 3) {
      if (!tree[l1_name][l2_name][l3_name]) {
        tree[l1_name][l2_name][l3_name] = [];
      }
    }
    if (level >= 4) {
      tree[l1_name][l2_name][l3_name].push(item);
    }
  });
  return tree;
};

const sortList = (list) => {
  list.sort((a, b) => {
    return (b.roic ?? -999) - (a.roic ?? -999);
  });

  return list;
};

const openGoogle = (text) => {
  window.open(`https://www.google.com/search?q=${text}`, "_blank");
};

const PageHome = (props) => {
  const { my, setMy, handleRouteChange, stocks } = props;

  const [currentTsCode, setCurrentTsCode] = useState(null);
  const [l1List, setL1List] = useState([]);
  const [tree, setTree] = useState({});

  useEffect(() => {
    const t = listToTree(stocks, 4);

    const l1_names = Object.keys(t)?.sort((a, b) => {
      let aIndex = l1Sort.findIndex((v) => v === a);
      aIndex = aIndex === -1 ? 999 : aIndex;
      let bIndex = l1Sort.findIndex((v) => v === b);
      bIndex = bIndex === -1 ? 999 : bIndex;

      return aIndex - bIndex;
    });

    setL1List(l1_names);
    setTree(t);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [my]);

  return (
    <>
      <div className={styles.container}>
        <div className={styles.leftContent}>
          <div>
            {l1List?.map((k1) => {
              if (!tree[k1]) return null;

              return (
                <Fragment key={k1}>
                  <br />
                  <div className="anchor" id={k1} data-name={k1}></div>
                  <Divider orientation="left" style={{ color: "rgb(255 0 0)" }}>
                    <span
                      className={styles.pointer}
                      onClick={() => openGoogle(k1)}
                    >
                      {k1}
                    </span>
                  </Divider>
                  {Object.keys(tree[k1]).map((k2) => {
                    return (
                      <Fragment key={k2}>
                        <div className={styles.paddingRight20}>
                          <span
                            className={styles.pointer}
                            onClick={() => openGoogle(k2)}
                          >
                            {k2}
                          </span>
                        </div>
                        {Object.keys(tree[k1][k2]).map((k3) => {
                          const k3List = sortList(tree[k1][k2][k3]);

                          return (
                            <Fragment key={k3}>
                              <div className={styles.paddingRight40}>
                                <span
                                  className={styles.pointer}
                                  onClick={() => openGoogle(k3)}
                                >
                                  {k3}
                                </span>
                              </div>
                              <div className={styles.paddingRight60}>
                                {k3List.map((item, index) => {
                                  const { name, ts_code, roic } = item;
                                  const { isBuy, tag } = my?.[ts_code] || {};
                                  let tagStyle = {};
                                  if (isBuy) {
                                    tagStyle = {
                                      color: "red",
                                      border: "1px solid red",
                                    };
                                  } else if (tag) {
                                    tagStyle = {
                                      border: "1px solid red",
                                    };
                                  }

                                  return (
                                    <span
                                      key={`${ts_code}_${index}`}
                                      className={classnames(styles.tagStyle)}
                                      style={tagStyle}
                                      onClick={() => {
                                        setCurrentTsCode(ts_code);
                                      }}
                                    >
                                      <span>{name.slice(0, 4)}</span>
                                      <span>
                                        {roic ? `(${roic.toFixed(1)})` : ""}
                                      </span>
                                    </span>
                                  );
                                })}
                              </div>
                            </Fragment>
                          );
                        })}
                      </Fragment>
                    );
                  })}
                </Fragment>
              );
            })}
          </div>
        </div>
        <div className={styles.rightNav}>
          <Space size="small" direction="vertical">
            <div>Count: {stocks.length}</div>
            <Button
              block
              size="small"
              type="dashed"
              onClick={() => handleRouteChange("my")}
            >
              
            </Button>
          </Space>
          <Space
            size="small"
            direction="vertical"
            className={styles.wrapAnchor}
          >
            {l1List?.map((name) => {
              return (
                <Button
                  style={{ color: "#2a5ea7" }}
                  key={name}
                  size="small"
                  type="link"
                  href={`#${name}`}
                >
                  {name}
                </Button>
              );
            })}
          </Space>
        </div>
      </div>

      <ModuleDetail
        ts_code={currentTsCode}
        my={my}
        setMy={setMy}
        stocks={stocks}
      />
    </>
  );
};

export default PageHome;
