import { useEffect, useState } from "react";
import { Button, Divider, Space, Input } from "antd";

import styles from "./index.module.css";

import ModuleDetail from "../ModuleDetail";
import ModuleK from "../ModuleK";

const PageMy = (props) => {
  const { handleRouteChange, my, setMy, stocks } = props;

  const [currentTsCode, setCurrentTsCode] = useState(null);
  const [myList, setMyList] = useState([]);
  const [keyword, setKeyword] = useState("");

  useEffect(() => {
    if (my) {
      const list = [];
      Object.keys(my)?.forEach((ts_code) => {
        list.push(my[ts_code]);
      });
      list.sort((a, b) => {
        if (Boolean(a.isBuy) !== Boolean(b.isBuy)) {
          return (b.isBuy ? 1 : 0) - (a.isBuy ? 1 : 0);
        }

        return (b.tag ? b.tag : 0) - (a.tag ? a.tag : 0);
      });
      setMyList(list);
    }
  }, [my]);

  return (
    <>
      <div className={styles.container}>
        <div className={styles.leftContent}>
          {myList
            ?.filter((item) => {
              if (keyword) {
                const { name, ts_code } = item;
                return name?.includes(keyword) || ts_code?.includes(keyword);
              }
              return true;
            })
            ?.map((item, index) => {
              const { name, ts_code, isBuy } = item;

              return (
                <div id={name} key={name}>
                  <Divider orientation="left">
                    <span
                      style={{
                        color: isBuy ? "rgba(255, 0, 0, 0.7)" : "",
                        cursor: "pointer",
                      }}
                      onClick={() => setCurrentTsCode(ts_code)}
                    >
                      {name}({index + 1})
                    </span>
                  </Divider>
                  <ModuleK ts_code={ts_code} />
                </div>
              );
            })}
        </div>
        <div className={styles.rightNav}>
          <Space size="small" direction="vertical">
            <div>Count: {myList.length}</div>
            <Button
              block
              size="small"
              type="dashed"
              onClick={() => handleRouteChange("/")}
            >
              
            </Button>
            <Input
              size="small"
              allowClear
              value={keyword}
              onChange={(e) => setKeyword(e.target.value)}
            />
          </Space>
          <Space
            size="small"
            direction="vertical"
            className={styles.wrapAnchor}
          >
            {myList.map((item) => {
              const { name, isBuy } = item;
              return (
                <Button
                  style={{ color: "#2a5ea7" }}
                  key={name}
                  size="small"
                  type="link"
                  href={`#${name}`}
                >
                  <span style={{ color: isBuy ? "rgba(255, 0, 0, 0.7)" : "" }}>
                    {name}
                  </span>
                </Button>
              );
            })}
          </Space>
        </div>
      </div>
      <ModuleDetail
        ts_code={currentTsCode}
        my={my}
        setMy={setMy}
        stocks={stocks}
      />
    </>
  );
};

export default PageMy;
